import Imagem from "@/components/Imagem";

const Hero = () => {
  return (
    <div className="relative w-full h-screen overflow-hidden">
      {/* Imagem de fundo */}
      <Imagem
        src="/assets/bg-image-hero.png"
        alt="Hero background"
        fill
        className="h-full w-full object-cover"
      />

      {/* Conteúdo centralizado sobre a imagem */}
      <div className="absolute inset-0 flex flex-col items-center justify-center gap-12 md:gap-20 lg:gap-32 z-10 px-4 text-center">
        <Imagem
          src="/assets/image-pretorian-hero.png"
          alt="Logo Central"
          width={300}
          height={300}
          className="object-contain w-64 sm:w-74 md:w-90 lg:w-[450px] h-auto"
        />

        <p className="border border-[#bfae67] rounded-3xl px-6 sm:px-8 md:px-10 py-2 text-base sm:text-lg md:text-xl lg:text-2xl font-extralight text-white max-w-[90%] sm:max-w-[80%] md:max-w-[70%]">
          Serviços Contábeis Especializados para o Mercado Financeiro
        </p>
      </div>
    </div>
  );
};

export default Hero;
